import React, { Suspense } from "react";
import Loader from "../utils/Loader";
import "./index.css";
import ScrollContainer from "../utils/ScrollContainer";
const HomePage = React.lazy(() => import("./HomePage"));
export default class Home extends React.PureComponent {
  ScrollContainer_ = React.createRef();
  homepage_ = React.createRef();
  componentDidMount() {
    this.setState({ scroll: this.ScrollContainer_.current.scrollContainer });
  }
  onScroll(e) {
    // console.log(e, this.homepage_?.current);
  }
  render() {
    return (
      <ScrollContainer ref={this.ScrollContainer_} onScroll_={this.onScroll}>
        <div>
          <Suspense fallback={<Loader />}>
            <HomePage ref={this.homepage_} scroll={this.state?.scroll} />
          </Suspense>
        </div>
      </ScrollContainer>
    );
  }
}
