import React from "react";
import styled from "styled-components";
import { SpinnerCircular } from "spinners-react";
const LoadContainer = styled.div`
  position: fixed;
  width: 100%;
  height: 100%;
  top: 0;
  left: 0;
  background: #262626;
`;
export default class Loader extends React.PureComponent {
  render() {
    return (
      <LoadContainer className="preloader">
        <div className="preloader__content">
          <div className="preloader__circle">
            <SpinnerCircular
              size="100%"
              thickness={8}
              color="#ffffff"
              secondaryColor="#555555"
              speed={110}
            />
          </div>
          <div className="preloader__header">
            <div className="preloader__heading h2-light h2">Nitesh Kumar</div>
            <div className="preloader__subline small-caps mt-1">
              Creative Developer
            </div>
          </div>
          <div className="preloader__counter h5 h5-light">&nbsp;</div>
        </div>
        <div className="preloader__curtain"></div>
      </LoadContainer>
    );
  }
}
